"use client";

import { useEffect, useRef } from "react";
import type p5 from "p5";

interface P5CanvasProps {
  sketch: (p: p5) => void;
  className?: string;
}

/**
 * P5Canvas Component
 *
 * p5.js„Çπ„Ç±„ÉÉ„ÉÅ„ÇíReact„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„Å®„Åó„Å¶ÊèèÁîª„Åó„Åæ„Åô„ÄÇ
 * Â§ñÈÉ®„ÅÆp5„Éó„É≠„Ç∏„Çß„ÇØ„Éà„ÇíÂæå„Åã„ÇâÁµ±Âêà„Åó„ÇÑ„Åô„ÅÑ„Çà„ÅÜ„Å´Ë®≠Ë®à„Åï„Çå„Å¶„ÅÑ„Åæ„Åô„ÄÇ
 *
 * @param sketch - p5„Çπ„Ç±„ÉÉ„ÉÅÈñ¢Êï∞Ôºàsetup, drawÁ≠â„ÇíÂÆöÁæ©Ôºâ
 * @param className - ËøΩÂä†„ÅÆCSS„ÇØ„É©„ÇπÂêçÔºà„Ç™„Éó„Ç∑„Éß„É≥Ôºâ
 */
export default function P5Canvas({ sketch, className = "" }: P5CanvasProps) {
  const canvasRef = useRef<HTMLDivElement>(null);
  const p5Instance = useRef<p5 | null>(null);

  useEffect(() => {
    // p5„ÇíÂãïÁöÑ„Å´„Ç§„É≥„Éù„Éº„Éà„Åó„Å¶SSR„Ç®„É©„Éº„ÇíÂõûÈÅø
    import("p5").then((p5Module) => {
      const p5Constructor = p5Module.default;

      // p5„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÅÆÂàùÊúüÂåñ
      if (canvasRef.current && !p5Instance.current) {
        p5Instance.current = new p5Constructor(sketch, canvasRef.current);
      }
    });

    // „ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó: „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆ„Ç¢„É≥„Éû„Ç¶„É≥„ÉàÊôÇ„Å´p5„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÇíÁ†¥Ê£Ñ
    return () => {
      if (p5Instance.current) {
        p5Instance.current.remove();
        p5Instance.current = null;
      }
    };
  }, [sketch]);

  return <div ref={canvasRef} className={className} />;
}
